import { Navigate, useLocation } from 'react-router-dom'
import { useAuth } from '../../contexts/AuthContext'
import LoadingSpinner from '../ui/LoadingSpinner'

const ProtectedRoute = ({ children, requiredRole = null }) => {
  const { isAuthenticated, user, loading } = useAuth()
  const location = useLocation()

  // Show loading spinner while checking authentication
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-primary-cream">
        <LoadingSpinner size="large" text="جاري التحقق من الصلاحيات..." />
      </div>
    )
  }

  // Redirect to login if not authenticated
  if (!isAuthenticated) {
    return (
      <Navigate 
        to="/login" 
        state={{ from: location }} 
        replace 
      />
    )
  }

  // Check role-based access
  if (requiredRole && user?.role !== requiredRole) {
    // Special handling for bookstore owners who haven't registered a bookstore yet
    if (requiredRole === 'bookstore_owner' && user?.role === 'bookstore_owner') {
      // Allow access to bookstore registration page
      if (location.pathname === '/bookstore/register') {
        return children
      }
      
      // For other bookstore routes, redirect to registration if no bookstore exists
      return <Navigate to="/bookstore/register" replace />
    }

    // For other role mismatches, show access denied
    return (
      <div className="min-h-screen flex items-center justify-center bg-primary-cream">
        <div className="text-center max-w-md mx-auto p-8">
          <div className="w-20 h-20 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <svg 
              className="w-10 h-10 text-red-600" 
              fill="none" 
              stroke="currentColor" 
              viewBox="0 0 24 24"
            >
              <path 
                strokeLinecap="round" 
                strokeLinejoin="round" 
                strokeWidth={2} 
                d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" 
              />
            </svg>
          </div>
          
          <h1 className="text-2xl font-bold text-gray-900 mb-4">
            غير مصرح لك بالوصول
          </h1>
          
          <p className="text-gray-600 mb-8">
            ليس لديك الصلاحيات اللازمة للوصول إلى هذه الصفحة.
            {requiredRole === 'admin' && ' هذه الصفحة مخصصة للمديرين فقط.'}
            {requiredRole === 'bookstore_owner' && ' هذه الصفحة مخصصة لأصحاب المكتبات فقط.'}
          </p>
          
          <div className="space-y-4">
            <button
              onClick={() => window.history.back()}
              className="btn-primary w-full"
            >
              العودة للصفحة السابقة
            </button>
            
            <Navigate to="/" className="btn-outline w-full">
              الذهاب للصفحة الرئيسية
            </Navigate>
          </div>
        </div>
      </div>
    )
  }

  // User is authenticated and has the required role
  return children
}

export default ProtectedRoute
